.calculator {
    max-width: 300px;
    margin: 0 auto;
    height: 500px;
    background-color: #f2f2f2;
    border: 2px solid #333;
    border-radius: 10px;
    /* padding: 20px; */
  }
  
  h1 {
    text-align: center;
    font-size: 24px;
    margin: 10px 0;
  }
  
  .display {
    /* border: 2px solid #333; */
    /* border-radius: 10px; */
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    padding: 10px;
    background-color: #181879;
    margin-bottom: 0px;
    height: 200px;
  }
  
  input[type="text"] {
    width: 100%;
    /* padding: 10px; */
    background-color: #181879;
    font-size: 18px;
    margin-bottom: 10px;
    border: none;
    border-bottom: 2px solid #333;
    color: white;
  }
  
  .history {
    color: white;
    border-radius: 10px;
    padding: 10px;
    text-align: right;
  }
  
  .history p {
    font-weight: bold;
    margin: 0 0 10px;
  }
  
  ul {
    list-style-type: none;
    padding: 0;
  }
  
  li {
    margin-bottom: 5px;
  }
  
  .buttons {
    margin-top: 0px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
  }
  
  button {
    width: 100%;
    height: 70px;
    padding: 10px;
    font-size: 20px;
    background-color: rgb(0, 12, 37);
    color: white;
    border: none;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #2980b9;
  }